.home-web {
    width: 100%;
    z-index: 5;
}
.home-web-wrapper {
    background: -webkit-radial-gradient(52% 93%, circle, #023365, #000000);
    background: -o-radial-gradient(52% 93%, circle, #023365, #000000);
    background: radial-gradient(circle at 52% 93%, #023365, #000000);
    width: 100%;
    height: 100%;
    min-height: 525px;
    min-width: 800px;
}
.home-wrapper {
    width: 100%;
    height: 100%;
}
.home-mobile {
    overflow-y: hidden;
    width: 100%;
    height: 100%;
}
.home-web {
    min-width: 800px;
    /*min-height: 510px;*/
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    position: relative;
    top: 50%;
    -webkit-transform: translate(0 ,-50%);
        -ms-transform: translate(0 ,-50%);
            transform: translate(0 ,-50%);
}
.trans-loader {
    position: fixed;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
        -ms-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}

.animated-page-wrapper {
    position: absolute;
    top: 62px;
    left: 0;
    width: 100%;
    height: 100%;
}




