.mobile-header-chat {
    opacity: 0.78;
    -webkit-box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.09);
            box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.09);
    border-bottom: solid 1px #595959;
    background-color: #272727;
    width: 100%;
    height: 7.5%;
    /*position: absolute;*/
    /*top: 0;*/
    /*left: 0;*/
    color:#dbdbdb;
}
.dot-wrapper {
    position: relative;
    margin-right: 8px;
    margin-top: auto;
    margin-bottom: auto;
}
.status {
    font-family: HelveticaNeue, Alef-Regular, sans-serif;
    font-size: 15px;
    font-weight: 500;
    font-style: normal;
    font-stretch: normal;
    line-height: normal;
    letter-spacing: 1.2px;
    text-align: right;
    /*color: #292929;*/
    margin: auto;
}
.status-glow {
    width: 6px;
    height: 6px;
    -webkit-filter: blur(3px);
    filter: blur(3px);
    background-color: #00cd16;
    border-radius: 80px;
    position: absolute;
    top:0;
    left:0;
}
.status-dot {
    border-radius: 80px;
    width: 6px;
    height: 6px;
    background-color: #00cd16;
    position: relative;
    -webkit-animation: blink 1.2s infinite;
            animation: blink 1.2s infinite;
}
.status-wrapper {
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    margin: auto;
    height: 100%;
    /*padding: 4.75%;*/
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;

}


